import React from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function HomeOurCustomerSay() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };
  return (
    <section>
      <Slider {...settings}>
        <div>col1</div>
        <div>col2</div>
      </Slider>
    </section>
  )
}
