import ProductCart from '@/app/common/ProductCart'
import React from 'react'

export default function HomeProducts({ productData }) {
  return (
    <>
      <div className="relative flex items-center justify-center">
        <div className="absolute w-full border-t border-gray-200"></div>
        <div className="relative z-10 bg-white flex">
          <button className="px-6 py-2 border border-red-300 text-red-400 font-semibold">
            Featured
          </button>
          <button className="px-6 py-2 border border-gray-200 font-semibold">
            New Arrivals
          </button>
          <button className="px-6 py-2 border border-gray-200 font-semibold">
            Onsale
          </button>
        </div>
      </div>

      <section className='py-[50px]'>
        <div className='max-w-[1320px] mx-auto grid grid-cols-4 gap-5'>
          {
            productData.map((data, index) => <ProductCart key={index} data={data} />)
          }
        </div>
      </section>
    </>
  )
}
